import React from 'react';

const Evento = ({evento}) => {

    const {url, description, title} = evento

    return (
        <div className="list-group mb-1">
            <a href={url} className="list-group-item list-group-item-action flex-column align-items-start active">
                <div className="d-flex w-100 justify-content-between">
                <h5 className="mb-1">{title}</h5>
                </div>
                <p className="mb-1">{description.substr(0,150)}</p>                
            </a>
        </div>
    );
};

export default Evento;